import React, {Component} from 'react';
import axios from 'axios';
import {reactLocalStorage} from 'reactjs-localstorage';

class Viewaccount extends Component {
    constructor(props){
        super(props);
        this.state ={
            data : '',
            load:false
        }
    
    }

    componentDidMount(){
        if(this.state.load==false){
            var token= reactLocalStorage.get('auth', true);
            const AuthStr = "Bearer ".concat(token); 
            axios.get('http://13.229.249.101/dashboard/api/userapi/account/view', { headers: { Authorization: AuthStr } })
            .then(response => {
                // If request is good...
                console.log(response.data);
                this.setState({"load":true, "data": response.data})
              })
            .catch((error) => {
                this.setState({"load":true})
                console.log('error ' + error);
            });
        }
    }

    render(){
        return (
            <div className="col-md-8 right-content">
                <div className="row">
                    <div className="col-12 border-bottom mb-4">
                        <h2>Account</h2>
                    </div>
                    <div className="col-md-4 py-md-2">Email</div>
                    <div className="col-md-8 py-md-2 text-capitalize">{this.state.data.name}</div>
                    <div className="col-md-4 py-md-2">Nama</div>
                    <div className="col-md-8 py-md-2 text-capitalize">{this.state.data.email}</div>
                    <div className="col-md-4 py-md-2">Contact</div>
                    <div className="col-md-8 py-md-2 text-capitalize">{this.state.data.contact}</div>
                    <div className="col-md-4 py-md-2">Address</div>
                    <div className="col-md-8 py-md-2 text-capitalize">{this.state.data.address}</div>
                    <div className="col-md-4 py-md-2">Province</div>
                    <div className="col-md-8 py-md-2 text-capitalize">{this.state.data.provinsiName}</div>
                    <div className="col-md-4 py-md-2">Kota / Kabupaten</div>
                    <div className="col-md-8 py-md-2 text-capitalize">{this.state.data.kotaName}</div>
                    <div className="col-md-4 py-md-2">Kelurahan</div>
                    <div className="col-md-8 py-md-2 text-capitalize">{this.state.data.kelurahanName}</div>
                    <div className="col-md-4 py-md-2">Kode Pos</div>
                    <div className="col-md-8 py-md-2 text-capitalize">{this.state.data.postalCode}</div>
                    <div className="col-12 py-4">
                    <button className="btn btn-dark py-1 px-3">Edit Account</button>
                    </div>
                </div>
            </div>
        );
    }
}

export default Viewaccount;