import React ,{Component} from 'react';
import Leftmenu from '../../components/Accounts/Leftmenu/Leftmenu';
import Viewaccount from '../../components/Accounts/Rightcontent/Viewaccount';
import Editaccount from '../../components/Accounts/Rightcontent/Editaccount';
import OrderHistory from '../../components/Accounts/Rightcontent/OrderHistory';
import BalanceHistory from '../../components/Accounts/Rightcontent/BalanceHistory';
import {Route} from "react-router-dom";
class Accounts extends Component {
    constructor(props){
        super(props);
        this.state ={

        }
    }
    render() {
        return(
            <div className="container">
                <div className="row">
                    <Leftmenu></Leftmenu>
                    <Route exact path="/account/" component={Viewaccount}/>
                    <Route path="/account/edit" component={Editaccount}/>
                    <Route path="/account/orderhistory" component={OrderHistory}/>
                    <Route path="/account/balancehistory" component={BalanceHistory}/>
                </div>
            </div>
        );
    }
}

export default Accounts;