import React, {Component} from 'react';
import {
    Link
  } from "react-router-dom";
import $ from 'jquery';
import './Leftmenu.css';

class Leftmenu extends Component {
    constructor(props){
        super(props);
        this.state ={
            statuscollapse : ''
        }
    }
    componentDidMount() {
        $(".sub-l-menu li a").click(function(){
            $(this).closest('li').find('.sb').slideToggle();
        });
    }

    render(){
        return (
            <div className="col-md-4 left-menu">
                <div className="bg-dark py-2 px-3 text-white">Member</div>
                <div className="col-12">
                    <ul className="mt-3 sub-l-menu">
                        <li>
                            <a><strong>Account</strong></a>
                            <ul className="mt-2 px-2 sb">
                                <li className="py-1"><Link to={process.env.PUBLIC_URL+"/account/edit"}>Manage Account</Link></li>
                                <li className="py-1"><Link to={process.env.PUBLIC_URL+"/account/orderhistory"}>Order History</Link></li>
                                <li className="py-1">Pending Order</li>
                                <li className="py-1"><Link to={process.env.PUBLIC_URL+"/account/balancehistory"}>Balance History</Link></li>
                                <li className="py-1">Order Report</li>
                            </ul>
                        </li>
                        <li className="sub-l-menu">
                            <a><strong>Marketing</strong></a>
                            <ul className="mt-2 px-2 sb">
                                <li className="py-1">Manage Account</li>
                                <li className="py-1">Order History</li>
                                <li className="py-1">Pending Order</li>
                                <li className="py-1">Balance History</li>
                                <li className="py-1">Order Report</li>
                            </ul>
                        </li>
                        <li>
                            <strong>Logout</strong>
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}

export default Leftmenu;