import React, {Component} from 'react';
import axios from 'axios';
import {reactLocalStorage} from 'reactjs-localstorage';
import "../Rightcontent/Rightcontent.css";
class OrderHistory extends Component {
    constructor(props){
        super(props);
        this.state ={
            receiverName : "",
            receiverContact : "",
            receiverAddress : "",
            payment : "",
            total : "",
            courier : "",
            page : "",
            orderHistory : []
        }
        this.timer = null;
        const payload=[{'key':"page", 'operator':"=", 'value': 1}];
        var token= reactLocalStorage.get('auth', true);
        const AuthStr = "Bearer ".concat(token); 
        axios.post('http://13.229.249.101/dashboard/api/userapi/order/find', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            this.setState({"orderHistory" : response.data});
        })
        .catch((error) => {
            this.setState({error: 'Periksa Kembali Data Pada From Edit Account'})
            console.log(error);
        });
    }

    componentDidUpdate (prevProps, prevState) {
        if((prevState.receiverName !== this.state.receiverName) || (prevState.receiverContact !== this.state.receiverContact) || (prevState.receiverAddress !== this.state.receiverAddress) || (prevState.payment !== this.state.payment) || (prevState.total !== this.state.total) || (prevState.courier !== this.state.courier) || (prevState.page !== this.state.page)) {
          this.handleCheck();
        }
    }
    handleCheck = () => {
        // Clears running timer and starts a new one each time the user types
        clearTimeout(this.timer);
        this.timer = setTimeout(() => {
            const data=[];
            if(this.state.receiverName != ''){
            data.push({'key':"receiverName", 'operator':"like", 'value': this.state.receiverName})
            }
            if(this.state.receiverContact != ''){
            data.push({'key':"receiverContact", 'operator':"like", 'value': this.state.receiverContact})
            }
            if(this.state.receiverAddress != ''){
            data.push({'key':"receiverAddress", 'operator':"like", 'value': this.state.receiverAddress})
            }
            if(this.state.payment != ''){
            data.push({'key':"payment", 'operator':"=", 'value': this.state.payment})
            }
            if(this.state.total != ''){
            data.push({'key':"grandTotal", 'operator':"=", 'value': this.state.total})
            }
            if(this.state.courier != ''){
            data.push({'key':"shippingName", 'operator':"like", 'value': this.state.courier})
            }
            if(this.state.page!= ''){
                data.push({'key':"page", 'operator':"=", 'value': this.state.page})
            }
            else{
                data.push({'key':"page", 'operator':"=", 'value': 1})
            }

                const payload=data;
                var token= reactLocalStorage.get('auth', true);
                const AuthStr = "Bearer ".concat(token); 
                axios.post('http://13.229.249.101/dashboard/api/userapi/order/find', payload, { headers: { Authorization: AuthStr } })
                .then((response) => {
                    this.setState({"orderHistory" : response.data});
                })
                .catch((error) => {
                  this.setState({error: 'Periksa Kembali Data Pada From Edit Account'})
                    console.log(error);
                });
        }, 1000);
    }
    changeStateHandler = (e) => {
        let change = {}
        change[e.target.name] = e.target.value
        this.setState(change)
    }
    render (){
        return (
            <div className="col-md-8 right-content">
                <div className="row">
                    <div className="col-12 border-bottom mb-4">
                        <h2>Order History</h2>
                    </div>
                    <div className="col-12 px-1">
                    <p>Filter By:</p>
                    </div>
                    <div className="col-md-3 px-1 mb-2">
                        <input type="text" className="form-control" name="receiverName" value={this.state.receiverName} onChange={this.changeStateHandler} placeholder="Receiver Name"></input>
                    </div>
                    <div className="col-md-3 px-1 mb-2">
                        <input type="text" className="form-control" name="receiverContact" value={this.state.receiverContact} onChange={this.changeStateHandler} placeholder="Receiver Contact"></input>
                    </div>
                    <div className="col-md-3 px-1 mb-2">
                        <input type="text" className="form-control" name="receiverAddress" value={this.state.receiverAddress} onChange={this.changeStateHandler} placeholder="Receiver Address"></input>
                    </div>
                    <div className="col-md-3 px-1 mb-2">
                        <select name="payment" className="form-control" onChange={this.changeStateHandler} >
                            <option value="">-Select-</option>
                            <option value="Yes">Yes</option>
                            <option value="No">No</option>
                        </select>
                        {/* <input type="text" className="form-control" name="payment" value={this.state.payment} onChange={this.changeStateHandler} placeholder="Payment"></input> */}
                    </div>
                    <div className="col-md-3 px-1 mb-2">
                        <input type="text" className="form-control" name="total" value={this.state.total} onChange={this.changeStateHandler} placeholder="Total"></input>
                    </div>
                    <div className="col-md-3 px-1 mb-2">
                        <input type="text" className="form-control" name="courier" value={this.state.courier} onChange={this.changeStateHandler} placeholder="Courier"></input>
                    </div>
                    <div className="col-md-3 px-1 mb-2">
                        <label className="float-left pt-2 pr-1 mb-0">Page</label> <input name="page" type="number" value={this.state.page} onChange={this.changeStateHandler}  className="form-control mw-100" placeholder="1"></input>
                    </div>
                </div>
                <div className="row mt-3 py-2 border-top border-bottom">
                    <div className="col-lg-2"><strong>#</strong></div>
                    <div className="col-lg-6"><strong>Detail</strong></div>
                    <div className="col-lg-2"><strong>Payment</strong></div>
                    <div className="col-lg-2"><strong>Grand Total</strong></div>
                </div>
                {
                this.state.orderHistory.map((history,i) => {
                    return(
                    <div className="row py-2" key={i}>
                        <div className="col-lg-2">
                        {history.id}
                        <button className="btn btn-light w-100 b-order" onClick={()=> window.open("http://13.229.249.101/website/print/order/"+history.id, "_blank")}>Print</button>
                            <button className="btn btn-light w-100 b-order mt-1">Track</button>
                        </div>
                        <div className="col-lg-6">
                        <p>{history.addTime}<br/>Location : {history.locationName}</p>
                        <p>Total Quantity: {history.totalQuantity} pcs</p>
                        <hr/>
                        <p>Sender:<br/>{history.senderName} ({history.senderContact})</p>
                        <p>Receiver:<br/>
                           {history.receiverName} ({history.receiverContact})<br/>
                           {history.receiverAddress}
                        </p>
                        <p>
                            {history.shippingName} ({history.shippingPrice})
                        </p>
                        </div>
                        <div className="col-lg-2">{history.payment}</div>
                        <div className="col-lg-2">{history.grandTotal}</div>
                    </div>
                    );
                })
                }
            </div>
        );
    }
}

export default OrderHistory;