import React, {Component} from 'react';
import axios from 'axios';
import {reactLocalStorage} from 'reactjs-localstorage';
import './Rightcontent.css';

class BalanceHistory extends Component{
    constructor(props){
        super(props);
        this.state ={
            page : 1,
            balanceHistory : []
        }
        this.timer = null;
        this.loadBalance() 
    }
    componentDidUpdate (prevProps, prevState) {

        if(prevState.page!== this.state.page) {
          this.handleCheck();
        }
    }
    loadBalance = () => {
        var token= reactLocalStorage.get('auth', true);
                const AuthStr = "Bearer ".concat(token); 
                axios.get('http://13.229.249.101/dashboard/api/userapi/deposit/view/page/'+this.state.page , { headers: { Authorization: AuthStr } })
                .then((response) => {
                    console.log(response);
                    this.setState({"balanceHistory" : response.data});

                    console.log(this.state.balanceHistory);
                 
                })
                .catch((error) => {
                  this.setState({error: 'Periksa Kembali Data Pada From Edit Account'})
                    console.log(error);
                });
    }
    handleCheck = () => {
        // Clears running timer and starts a new one each time the user types
        clearTimeout(this.timer);
        this.timer = setTimeout(() => {
           this.loadBalance()   
        }, 1000);
    }
    changePageHanlder = (e) =>{
        let change = {}
        change[e.target.name] = e.target.value
        this.setState(change)
    }
    render(){
        return(
            <div className="col-md-8 right-content-edit">
                <div className="row">
                        <div className="col-12 text-center">
                            <h2 className="text-left">Balance History</h2>
                            <label><span className="float-left pt-2 pr-2">Page : </span><input type="number" name="page" className="float-left form-control mw-100" min="1" value={this.state.page} onChange={this.changePageHanlder}></input></label>
                        </div>
                </div>
                <div className="row mt-2 py-2 border-top border-bottom">
                            <div className="col-lg-1"><strong>#</strong></div>
                            <div className="col-lg-3"><strong>Description</strong></div>
                            <div className="col-lg-2"><strong>Operation</strong></div>
                            <div className="col-lg-2"><strong>Amount</strong></div>
                            <div className="col-lg-2"><strong>Total</strong></div>
                            <div className="col-lg-2"><strong>Time</strong></div>
                </div>
                {
                    this.state.balanceHistory.map((balance, i) => {
                        return (
                            <div className="row mt-2 py-1 border-bottom" key={i}>
                                <div className="col-lg-1">{balance.id}</div>
                                <div className="col-lg-3">{balance.description}</div>
                                <div className="col-lg-2">{balance.operation}</div>
                                <div className="col-lg-2">{balance.amount}</div>
                                <div className="col-lg-2">{balance.total}</div>
                                <div className="col-lg-2">{balance.time}</div>
                            </div>
                        )
                    })
                }
            </div>
        );
    }
}

export default BalanceHistory;