import React, { Component } from 'react';
import axios from 'axios';
class ColorView extends Component { 
    constructor(props) {
        super(props);
        this.state = {
          products: [],
          stock: [],
          color:[],
          size:[],
          data:[],
          warna:"",
          price:[]
        };
      }

      componentDidMount() {
        var headers = {
            'Content-Type': 'application/json'
        }
        axios.get("http://13.229.249.101/dashboard/api/onlineproduct/view/category/"+this.props.idCategory, {headers: headers})
          .then(function(respons){
              return respons.data;
            }).then(data => {
               this.setState({'data':data});
               var json = data;
               var arr = [];
               var color = [];
                    Object.keys(json).forEach(function(key,index) {
                        color.push(key);
                        arr.push(json[key]);
                    });
                    
                    this.setState({'color': color});
                    this.setState({'stock':arr});
                    arr.map(ar=>{
                        var arr2 = [];
                        Object.keys(ar).forEach(function(key) {
                            arr2.push(ar[key]);
                        });
                    arr2.map(ar2=>{
                        var size = [];
                        Object.keys(ar2).forEach(function(key) {
                            size.push(key);
                        });
                    this.setState({'size' : size});
                    ///warna default/////////////////////////
                    if(size[0] == 'SM' || size[0] == 'OSFA' || size[0] == 'Stock'){
                        this.setState({"warna": color[0]});
                    }
                    else{
                        this.setState({"warna": 'Black'});
                    }
                    ///warna default/////////////////////////
                })
              });

            })
        axios.get("http://13.229.249.101/dashboard/api/onlineproductdetail/view/category/87 ", {headers: headers})
            .then(function(respons){
                 return respons.data;
              }).then(data => {
                this.setState({'price':data.price});
              })
      }

clickColorHandler = (warna) => {
    this.setState({"warna":warna})
}

render (){
    ////untuk tukar posisi size//////////////////////////////////////////////////////////
    const tukarSize = this.state.size;
    if(tukarSize[0]!='S' && (tukarSize[0]!="SM" && tukarSize[0]!="OSFA" && tukarSize[0]!="Stock")){
        [tukarSize[0], tukarSize[1], tukarSize[2],tukarSize[3],tukarSize[4]] = [tukarSize[1], tukarSize[2], tukarSize[3],tukarSize[0],tukarSize[4]];
    }
    /////////////////////////////////////////////////////////////////////////////////////
    ////untuk tampilkan stock ke table///////////////////////////////////////////////////
    const allStock = this.state.data;
    const war= this.state.warna;
    const daftaStock =Object.keys(allStock).map(function(key) {
        if(key==war){
            var detailAllStock = allStock[key];
            return Object.keys(detailAllStock).map((item, index) => {
                const dataStock=detailAllStock[item];
                if(tukarSize[0]!="SM" && tukarSize[0]!="OSFA" && tukarSize[0]!="Stock"){
                    var tukarPosisiStock=[''+dataStock['S'],''+dataStock['M'],''+dataStock['L'],''+dataStock['XL'],''+dataStock['2XL']];
                }
                else if( tukarSize[0] == "SM"){
                    var tukarPosisiStock=[''+dataStock['SM']];    
                }
                else if( tukarSize[0] == "OSFA"){
                    var tukarPosisiStock=[''+dataStock['OSFA']];    
                }
                else if( tukarSize[0] == "Stock"){
                    var tukarPosisiStock=[''+dataStock['Stock']];    
                }
                return (<tr key={index}>
                                <td className="text-left">{item}</td>
                                {
                                    tukarPosisiStock.map((item, index) => {
                                        return (<td  className="text-left" key={index}>{item}</td>);
                                    })
                                }
                        </tr>);
            })
        }
    });
    /////////////////////////////////////////////////////////////////////////////////////////
    //////////Menampilkan Warna//////////////////////////////////////////////////////////////
    const showColor = this.state.color.map((color, index) => {
        if(index == 0){
            var active = "active";
        }
        else{
            var active= "";
        }
        return (<li key={index} role="presentation" onClick={() => this.clickColorHandler(color)} className={active}><img src={process.env.PUBLIC_URL + '/img/product/color/'+color+'.jpg'} /><a href="">{color}</a></li>);
    });
    /////////////////////////////////////////////////////////////////////////////////////////
    return(
        <div className="row">
            <div className="col-lg-8 col-md-12 col-sm-12 order-lg-1 order-2">
                <div className="p-details-tab">
                    <ul className="nav nav-tabs color" role="tablist">
                        {/* ////////Pilihan Warna//////// */}
                        {showColor}
                        {/* ////////Pilihan Warna//////// */}
                    </ul>
                </div>
                <div className="tab-content review">
                    <div role="tabpanel" className="tab-pane active" id="more-info">
                        <div className="table-stock">
                            <table>
                                <tbody>
                                <tr>
                                    <th className="text-left"><img src={process.env.PUBLIC_URL + '/img/product/color/'+this.state.warna+'.jpg'}  width="60" /></th>
                                    {/* //////size////// */}
                                    {
                                        tukarSize.map((size,index) => {
                                        return <th className="text-left" key={index}>{size}</th>;
                                        })
                                    }
                                     {/* //////size////// */}
                                </tr>
                                {/* ////////Daftar Stock//////// */}
                                {daftaStock}
                                {/* ////////Daftar Stock//////// */}
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <div role="tabpanel" className="tab-pane" id="data">
                        <table className="table-data-sheet">
                            <tbody>
                                <tr className="odd">
                                    <td>Compositions</td>
                                    <td>Cotton</td>
                                </tr>
                                <tr className="even">
                                    <td>Styles</td>
                                    <td>Casual</td>
                                </tr>
                                <tr className="odd">
                                    <td>Properties</td>
                                    <td>Short Sleeve</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div role="tabpanel" className="tab-pane" id="reviews">
                        <div id="product-comments-block-tab">
                            <a href="#" className="comment-btn"><span>Be the first to write your review!</span></a>
                        </div>
                    </div>
                </div>
            </div>
            <div className="product-center-column col-lg-4 col-md-12 col-sm-12  order-lg-2 order-1">
                <h1>Price</h1>
                <div className="table-price">
                    <table className="w-100">
                        <tbody>
                        {
                            this.state.price.map((price, index) => {
                                const i= index;
                                return (<tr key={index}>
                                   {   
                                        Object.keys(price).map((iprice, index) => {
                                        if(i==0){
                                            return (<th key={index}>{price[iprice]}</th>);
                                        }
                                        else{
                                            return (<td key={index}>{price[iprice]}</td>);
                                        }
                                        })
                                            
                                   }
                          
                                </tr>);
                            })
                        }
                        </tbody>
                    </table>
                </div>
                <h1>Size</h1>
                <div className="table-price">
                    <table>
                        <tbody>
                        <tr>
                            <td colSpan="3"><img alt="" src={ require('../../../assets/img/product/size1.svg') } /></td>
                        </tr>
                        <tr><td colSpan="3" height="10"></td></tr>
                        <tr>
                            <th>Size</th>
                            <th>Width</th>
                            <th>Length</th>
                        </tr>
                        <tr>
                            <td>S</td>
                            <td>47 cm</td>
                            <td>67 cm</td>
                        </tr>
                        <tr>
                            <td>M</td>
                            <td>50 cm</td>
                            <td>70 cm</td>
                        </tr>
                        <tr>
                            <td>L</td>
                            <td>53 cm</td>
                            <td>73 cm</td>
                        </tr>
                        <tr>
                            <td>XL</td>
                            <td>56 cm</td>
                            <td>76 cm</td>
                        </tr>
                        <tr>
                            <td>2XL</td>
                            <td>59 cm</td>
                            <td>79 cm</td>
                        </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        
    )
}


}
export default ColorView;