import React, {Component} from 'react';
class ProductDescription extends Component{
    constructor(props){
        super(props);
        this.state = {
            link    : "http://13.229.249.101/dashboard/api/web/uploads/category/tag/"
        } 
    }
    render(){
        if(this.props.tag != undefined){
        const tags = this.props.tag;
        var tag = Object.keys(tags).map((tag,i)=>{ 
            const tg =tags[tag];
            if(i==0){
                var classt = "row mt-4";
            }
            else{
                var classt = "row mt-2";
            }
            return (<div className={classt} key={i}>
            {
                 Object.keys(tg).map((tag,is)=>{
                    if(i==0){
                        return(<div className="col-md-3 col-sm-3 text-center" key={is}><p className="bg-tag">{tg[tag]}</p><img src={ this.state.link+tg[tag]+".png" } /></div>)
                    }
                    else{
                        return(<div className="col-md-3 col-sm-3 text-tag" key={is}>{tg[tag]}</div>)
                    }
                     
                 })
            }
            </div>);
           
        });
        }
        return(
            <div className="product-center-column col-lg-8 col-md-8 col-sm-7">
        <h1>#{this.props.mill} {this.props.name}</h1>
        <div id="short-description-block">
        <div dangerouslySetInnerHTML={{__html: this.props.descriptionweb}}></div>
        
           {tag}
       
          
        </div>
        </div>
        );
    }
}
    
   

export default ProductDescription;