import React, {Component} from 'react';
import axios from 'axios';
import {reactLocalStorage} from 'reactjs-localstorage';
import "../Rightcontent/Rightcontent.css";

class Editaccount extends Component {
    constructor(props){
        super(props);
        this.state ={
            data : {
                email :"",
                name  :"",
                password :"",
                contact  :"",
                address  :"",
                kelurahanName : "",
                kotaName : "",
                provinsiName : "",
                postalCode : ""
            },
            kelurahan : "",
            idkelurahan : "",
            load:false,
            value: 0,
            kelurahans: [],
            kodepos:"",
            postalCode:[],
            show:false,
            statusupdate:""
        }
        this.timer = null;

    }

    componentDidMount(){
        this.timer = null;
        this.filteredKelurahan = "";
        if(this.state.load==false){
            var token= reactLocalStorage.get('auth', true);
            const AuthStr = "Bearer ".concat(token); 
            axios.get('http://13.229.249.101/dashboard/api/userapi/account/view', { headers: { Authorization: AuthStr } })
            .then(response => {
                // If request is good...
                console.log(response.data);
                this.setState({"load":true, "data": response.data, "idkelurahan": response.data.kelurahanId})
                
              })
            .catch((error) => {
                this.setState({"load":true})
                console.log('error ' + error);
            });
        }
    }

    componentDidUpdate (prevProps, prevState) {
        if(prevState.kelurahan !== this.state.kelurahan) {
          this.handleCheck();
        }
    }

    handleChange(e) {
        const val = e.target.value
        const na = e.target.name
        this.setState(state => {
            state.data[na] = val
            return state
        })
    }

    handleCheck = () => {
        // Clears running timer and starts a new one each time the user types
        clearTimeout(this.timer);
        this.timer = setTimeout(() => {
            this.kelurahanFunction(this.state.kelurahan); 
        }, 1000);
    }
    
    clickKodePosHanlder = (e) =>{
        var isi = e.currentTarget.textContent;
        this.setState(state => {
            state.data['postalCode'] = isi
            state.show = false
         return state;
        })
    }

    kelurahanFunction = (val) => {
        if(val!='' && val!=null && val!=undefined){
        var token= reactLocalStorage.get('auth', true);
        const AuthStr = "Bearer ".concat(token); 
        var data={
            "key": val
            }
        var headers = {
            'Content-Type': 'application/json',
            'Authorization' : AuthStr
            }
        axios.post('http://13.229.249.101/dashboard/api/userapi/kelurahan/find', data, headers)
        .then((response) => {
            this.setState({'kelurahans': response.data});  
            console.log(this.state.kelurahans) 
        })
        .catch((error) => {
            console.log(error);
        });
        }
        else{
           this.setState({'kelurahans': ''}); 
        }
    }

    kelurahanHandler = (e) =>{
        const val = e.target.value
        this.setState({
            "kelurahan" : val
        })
       
    }

    clickKelurahanHandler = (e) => {
        this.postalCode=e.target.getAttribute("data-kodepos");
        console.log(this.state.postalCode);
        this.setState({
            "show" : true,
            "kelurahan" : e.currentTarget.textContent,
            "postalCode" : this.postalCode.split('; '),
            "idkelurahan" : e.target.getAttribute("data-kelurahan")
        });
    }

    postalCodeHandler = (e) => {
        console.log(this.state.postalCode);
        const val = e.target.value
        this.setState(state => {
            state.data['postalCode'] = val
            state.show = true
            return state
        })      
        this.setState({"show":true});  
    }

  
    submitHandler = (event) => {
        const payload={
            "password" : this.state.data.password,
            "name": this.state.data.name,
            "address": this.state.data.address,
            "postalCode": this.state.data.postalCode,
            "contact": this.state.data.contact,
            "kelurahanId" : this.state.idkelurahan
            }
            console.log(payload);
            var token= reactLocalStorage.get('auth', true);
            const AuthStr = "Bearer ".concat(token); 
            axios.put('http://13.229.249.101/dashboard/api/userapi/account/edit', payload, { headers: { Authorization: AuthStr } })
            .then((response) => {
                this.setState({"statusupdate" : response.data.response.success});
                setTimeout(function() { 
                    this.setState({"statusupdate" : ""})
                }.bind(this), 5000);
             
            })
            .catch((error) => {
              this.setState({error: 'Periksa Kembali Data Pada From Edit Account'})
                console.log(error);
            });  
        event.preventDefault();     
          
    }

    render(){
        let filteredKodePos=this.state.postalCode.filter((kodepos) => {
            return kodepos.indexOf(this.state.data.postalCode) !== -1;
        });   

          
        return (
            <div className="col-md-8 right-content-edit">
                <form id="form-edit" className="row" onSubmit={this.submitHandler}>
                    <div className="col-12 border-bottom mb-4">
                        <h2>Account</h2>
                    </div>
                    <div className="col-12">
                        {
                        this.state.statusupdate == 'Update Success' ? <div className="alert alert-primary" role="alert">
                         Update Account Success
                        </div>: this.state.statusupdate != '' ?<div className="alert alert-danger" role="alert">
                         Update Account Failed
                        </div>:null
                        }
                    </div>
                    <div className="col-md-4 py-md-2">Email</div>
                    <div className="col-md-8 py-md-2 text-capitalize"><input className="form-control" type="text" name="email" value={this.state.data.email} onChange={this.handleChange.bind(this)} readOnly></input></div>
                    <div className="col-md-4 py-md-2">Password</div><div className="col-md-8 py-md-2 text-capitalize"><input className="form-control" type="password" placeholder="Password" name="password" onChange={this.handleChange.bind(this)}></input></div>
                    <div className="col-md-4 py-md-2">Nama</div>
                    <div className="col-md-8 py-md-2 text-capitalize"><input className="form-control" type="text" name="name" value={this.state.data.name} onChange={this.handleChange.bind(this)}></input></div>
                    <div className="col-md-4 py-md-2">Contact</div>
                    <div className="col-md-8 py-md-2 text-capitalize"><input className="form-control" type="text" name="contact" value={this.state.data.contact} onChange={this.handleChange.bind(this)}></input></div>
                    <div className="col-md-4 py-md-2">Address</div>
                    <div className="col-md-8 py-md-2 text-capitalize"><input className="form-control" type="text" name="address" value={this.state.data.address} onChange={this.handleChange.bind(this)}></input></div>
                    <div className="col-md-4 py-md-2">Kecamatan</div>
                    <div className="col-md-8 py-md-2 text-capitalize">
                    <input name="kelurahan" className="form-control" autoComplete="off" placeholder={this.state.data.kelurahanName+', '+this.state.data.kotaName+', '+this.state.data.provinsiName} value={this.state.kelurahan} onChange={this.kelurahanHandler} ></input>
                    { this.state.kelurahans != '' ?
                     <ul className="sugest">
                        {
                            this.state.kelurahans.map((kelurahan,i) => {      
                                return (<li key={i} onClick={this.clickKelurahanHandler} data-kelurahan={kelurahan.kelurahanId} data-kodepos={kelurahan.postalCode}>{kelurahan.kelurahanName+', '+kelurahan.kotaName+', '+kelurahan.provinsiName}</li>);
                            })
                        }
                    </ul>
                        :null
                    } 
                    </div>
                    <div className="col-md-4 py-md-2">Kode Pos</div>
                    <div className="col-md-8 py-md-2 text-capitalize">
                    <input name="postalcode" className="form-control" autoComplete="off" value={this.state.data.postalCode} onChange={this.postalCodeHandler}></input>
                    { filteredKodePos != '' && this.state.show == true ?
                     <ul className="sugest">
                        {
                            filteredKodePos.map((kodepos,i) => {
                                
                                return (<li key={i} onClick={this.clickKodePosHanlder}>{kodepos}</li>);
                            })
                        }
                    </ul>
                        :null
                    }
                    
                   </div>
                    <div className="col-12 py-4">
                    <button type="submit" className="btn btn-dark py-1 px-3">Submit</button>
                    </div>
                </form>
            </div>
        );
    }
}

export default Editaccount;