import React from 'react';

const breadcrumb = (props) => (
    // <!--Breadcrumb Start-->
    <div className="breadcrumb-area">
        <div className="container">
            <div className="row">
                <div className="col-md-12">
                    <div className="breadcrumb">
                        <a title="Return to Home" href="index.html" className="home"><i className="fa fa-home"></i>Home</a>
                        <span className="navigation">&gt;</span>{props.breadcrumb}
                    </div>
                </div>
            </div>
        </div>
    </div>
    // <!--End of Breadcrumb-->
)

export default breadcrumb;