import React, { Component } from 'react';
import {
  Route,
  BrowserRouter
} from "react-router-dom";
import axios from 'axios';
import {reactLocalStorage} from 'reactjs-localstorage';
import Layout from './components/Layout/Layout';
import FirstPage from './containers/FirstPage/FirstPage';
import DetailProducts from './containers/DetailProducts/DetailProducts';
import Accounts from './containers/Accounts/Accounts';
import Print from './containers/Print/Print';
import AuthContext from './context/auth-context';
class App extends Component {
  constructor(props){
    super(props);
    this.state ={
      authenticated:false,
      load:false
    }
  }


  componentDidMount(){
    if(this.state.load==false){
    var token= reactLocalStorage.get('auth', true);
    const AuthStr = "Bearer ".concat(token); 
    axios.get('http://13.229.249.101/dashboard/api/userapi/account/view', { headers: { Authorization: AuthStr } })
    .then(response => {
        this.setState({
          "authenticated": true,
          "load" : true
        })
     
      })
    .catch((error) => {
        this.setState({'authenticated': false,
        "load" : true
      });
      
       
    }); 
  } 
  
  
  }
  
  render() {  
    const paths = window.location.pathname;
    return (
      <BrowserRouter>
        <div className="App">
        <AuthContext.Provider 
          value={{
            authenticated: this.state.authenticated
          }}>
          { paths.includes("/print") ?
            <Route path="/print" component={Print}/>
            :<Layout>
            <Route exact path="/" component={FirstPage}/>
            <Route path="/details-product/:par" component={DetailProducts}/>
            <Route path="/account" component={Accounts}/>
            </Layout>
          }
        </AuthContext.Provider>
        </div>
      </BrowserRouter>

    );
  }
}

export default App;
